<template>
    <div class="modal" @click="$emit('showOrClose')">
        <div class="modal__content" @click.stop>
            <h3 class="modal__content-title">{{ id ? 'Edit note' : 'Add note' }}</h3>
            <form action="" class="modal__content-form" @submit.prevent="$emit('addOrChange', id)">
                <input type="text" class="modal__content-inp" placeholder="Title" required
                    @input="$emit('changeTitle', $event.target.value)" :value="title">
                <textarea class="modal__content-area" name="" id="" cols="30" rows="7" placeholder="Content" required
                    @input="$emit('changeDesc', $event.target.value)" :value="desc"></textarea>
                <div class="modal__content-btns">
                    <a href="#!" class="modal__content-cancel" @click="$emit('showOrClose')">Cancel</a>
                    <button class="modal__content-add">{{ id ? 'Edit' : 'Add' }}</button>
                </div>
            </form>
        </div>
        <!-- <div class="modal__question">
            <h2 class="modal__question-title">Return text</h2>
            <div class="modal__question-btns">
                <span class="modal__question-btn">Yes</span>
                <span class="modal__question-btn">No</span>
            </div>
        </div> -->
    </div>
</template>

<script>
export default {
    name: "Modal",
    props: {
        title: {
            type: String,
            default: ""
        },
        desc: {
            type: String,
            default: ""
        },
        id: {
            type: Number,
            default: null
        }
    }
}
</script>

<style lang="scss" scoped></style>