<template>
    <section class="notes">
        <div class="container">
            <div class="notes__content">
                <h2 class="notes__title">All notes</h2>
                <button class="notes__content-checker" @click="view = !view">
                    <img src="@/assets/img/icons/list.svg" alt="" class="notes__content-icon" v-show="view">
                    <img src="@/assets/img/icons/grid.svg" alt="" class="notes__content-icon" v-show="!view">
                    <b> {{ view ? "List" : "Grid" }}</b>
                </button>
            </div>
            <div class="notes__cards" :class="{ 'grid': !view }">
                <NotesItem v-for="(note, idx) in notes" :key="idx" 
                :note="note" 
                @removeTask="$emit('removeTask', note.id)"
                @editTask="$emit('editTask', note.id)"
                />
            </div>
        </div>
    </section>
</template>

<script>

import NotesItem from './NotesItem.vue'

export default {
    name: "Notes",
    data() {
        return {
            view: true,
        }
    },
    props: {
        notes: {
            type: Array,
            default: []
        }
    },
    components: {
        NotesItem
    }
}
</script>

<style>
.text-enter-active,
.text-leave-active {
    transition: 0.4s;
}

.text-enter-from,
.text-leave-to {
    opacity: 0;
}
</style>