<template>
  <Header @getSearch="search = $event" />
  <h1>{{ search }}</h1>
  <main>
    <Notes :notes="filterNotes" @removeTask="removeTask" @editTask="editTask" />
  </main>
  <a href="#!" class="openModal" @click="this.showModal = true, curId = null">
    <img src="./assets/img/icons/add-note.svg" alt="">
  </a>
  <Teleport to="body">
    <Transition name="modal">
      <Modal v-show="showModal" @showOrClose="closeModal" @changeTitle="modalTitle = $event"
        @changeDesc="modalDesc = $event" @addOrChange="addOrChange" :title="modalTitle" :desc="modalDesc" :id="curId" />
    </Transition>
  </Teleport>
</template>

<script>
import Header from "@/components/common/Header.vue";
import Notes from "@/components/notes/notes.vue"
import Modal from "@/components/UI/Modal.vue"

export default {
  name: "App",
  components: {
    Header,
    Notes,
    Modal
  },
  data() {
    return {
      notes: [
        {
          id: 1,
          title: "Hello",
          date: new Date(Date.now()).toLocaleString(),
          desc: "Lorem ipsum dolor 7"
        },
        {
          id: 2,
          title: "Salom 2",
          date: new Date(Date.now()).toLocaleString(),
          desc: "Lorem ipsum dolor 8"
        },
        {
          id: 3,
          title: "Hi 3",
          date: new Date(Date.now()).toLocaleString(),
          desc: "Lorem ipsum dolor 9"
        },
      ],
      showModal: false,
      modalTitle: "",
      modalDesc: "",
      curId: null,
      search: ""
    }
  },
  methods: {
    closeModal() {
      this.showModal = false
      setTimeout(() => {
        this.modalTitle = ""
        this.modalDesc = ""
        this.curId = null
      }, 300);
    },
    addOrChange(id) {
      let obj = {
        id: id || new Date().getTime(),
        title: this.modalTitle,
        date: new Date(Date.now()).toLocaleString(),
        desc: this.modalDesc,
      }
      // console.log(id);
      if (id) {
        let idx = this.notes.findIndex(item => item.id == id)
        // console.log(this.notes[idx]);
        this.notes[idx] = obj
      } else {
        this.notes.push(obj)
      }
      this.modalTitle = ""
      this.modalDesc = ""
      this.showModal = false
    },
    removeTask(id) {
      let idx = this.notes.findIndex(item => item.id == id)
      this.notes.splice(idx, 1)
    },
    editTask(id) {
      // console.log(id);
      this.showModal = true
      let editId = this.notes.find(item => item.id == id)
      // console.log(editId);
      this.modalTitle = editId.title
      this.modalDesc = editId.desc
      this.curId = editId.id
    }
  },
  computed: {
    filterNotes() {
      return this.search ? this.notes.filter((item) => {
        return item.title.toLowerCase().includes(this.search.toLowerCase())
        // return (
        //   item.title.toLowerCase().includes(this.search.toLowerCase()) || item.desc.toLowerCase().includes(this.search.toLowerCase())
        // )
      }) : this.notes
    }
  }
};
</script>

