<template>
  <header class="header">
    <h1 class="header__title">Notes</h1>
    <form action="" class="header__form">
      <input type="text" class="header__form-inp" placeholder="Search..." v-model="search" />
    </form>
  </header>
</template>

<script>

export default {
  name: "Header",
  data() {
    return {
      search: ""
    }
  },
  watch: {
    search(newVal, oldVal) {
      // console.log(oldVal);
      this.$emit("getSearch", newVal)
    }
  }
}

</script>