<template>
  <div class="notes__card">
    <h3 class="notes__card-title">{{ note.title }}</h3>
    <span class="notes__card-date">{{ note.date }}</span>
    <p class="notes__card-txt">
      {{ note.desc }}
    </p>
    <div class="notes__card-control">
      <a href="#!" class="notes__card-edit" @click="$emit('editTask', note.id)">
        <img
          src="@/assets/img/icons/edit.svg"
          alt=""
          class="notes__card-icon"
        />
        Edit
      </a>
      <a href="#!" class="notes__card-del" @click="$emit('removeTask', note.id)">
        <img
          src="@/assets/img/icons/delete.svg"
          alt=""
          class="notes__card-icon"
        />
        Delete
      </a>
    </div>
  </div>
</template>

<script>
export default {
  name: "NotesItem",
  props: {
    note: {
      type: Object,
      required: true,
    },
  },
};
</script>

